/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
  font-size: 62.5%;
  /* font-size 1em = 10px on default browser settings */
  font-family: Arial-BoldMT;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

@font-face {
  font-family: Arial-BoldMT;
  src: url(../assets/fonts/Arial-BoldMT.ttf);
}

@font-face {
  font-family: Arial-MT;
  src: url(../assets/fonts/Arial-MT.ttf);
}

body {
  color: #333333;
}

.icon {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  background-size: cover;
  background-repeat: no-repeat;
}

.icon_dbl-arrow {
  background-image: url(../assets/images/dbl-arrow.svg);
}

.icon_temperature {
  background-image: url(../assets/images/icon_temperature.svg);
}

.icon_sun {
  background-image: url(../assets/images/icon_sun.svg);
}

.icon_cloud {
  background-image: url(../assets/images/cloud-drizzle.svg);
}

.header {
  padding: 3rem 4rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-bottom: 1px solid #f0f0f0;
}

.header__logo {
  margin-right: 3rem;
}

.content {
  display: flex;
  flex-flow: row wrap;
  padding: 3rem 3rem;
}

.menu {
  display: flex;
  font-size: 1.6rem;
}

.menu__item {
  color: inherit;
  margin: 0 1rem;
  opacity: 0.6;
  cursor: pointer;
  transition: opacity ease 0.25s;
  font-weight: bolder;
}

.menu__item:hover {
  opacity: 1;
}

.menu__item_active {
  opacity: 1;
  cursor: default;
}

.input-range {
  display: inline-block;
  position: relative;
  height: 60px;
}

.input-range > input[type="range"] {
  -webkit-appearance: none;
  /* Hides the slider so that custom slider can be made */
  width: 584px;
  /* Specific width is required for Firefox. */
  height: 60px;
  outline: none;
}

.input-range > input[type="range"]::-webkit-slider-runnable-track {
  /* common styles */
  background-image: linear-gradient(to left, #ff9e00, #ffe682 100%);
  width: 584px;
  height: 60px;
  border-radius: 60px;
}

.input-range > input[type="range"]::-moz-range-track {
  /* common styles */
  background-image: linear-gradient(to left, #ff9e00, #ffe682 100%);
  width: 584px;
  height: 60px;
  border-radius: 60px;
}

.input-range > input[type="range"]::-ms-track {
  /* common styles */
  background-image: linear-gradient(to left, #ff9e00, #ffe682 100%);
  width: 584px;
  height: 60px;
  border-radius: 60px;
}

.input-range > input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

.input-range > input[type="range"]::-webkit-slider-thumb {
  width: 64px;
  height: 64px;
  border-radius: 100%;
  margin-top: -2px;
  border: 4px solid white;
  box-shadow: 0px 0px 0 4px rgba(51, 51, 51, 0.1) inset;
}

.input-range > input[type="range"]::-moz-range-thumb {
  width: 64px;
  height: 64px;
  border-radius: 100%;
  margin-top: -2px;
  border: 4px solid white;
  box-shadow: 0px 0px 0 4px rgba(51, 51, 51, 0.1) inset;
}

.input-range > input[type="range"]::-ms-thumb {
  margin-top: 0;
  width: 64px;
  height: 64px;
  border-radius: 100%;
  margin-top: -2px;
  border: 4px solid white;
  box-shadow: 0px 0px 0 4px rgba(51, 51, 51, 0.1) inset;
}

.input-range > input[type="range"]::-ms-tooltip {
  display: none;
}

.input-range::before, .input-range::after {
  position: absolute;
  font-size: 18px;
  pointer-events: none;
  color: #ffffff;
  letter-spacing: 0;
  text-align: left;
  top: calc(50% - 8px);
}

.input-range::before {
  left: 20px;
  content: attr(data-range-min);
}

.input-range::after {
  right: 20px;
  content: attr(data-range-max);
}

.input-range_vertical {
  width: 60px;
  height: 584px;
}

.input-range_vertical > input[type="range"] {
  transform-origin: left top;
  transform: rotate(-90deg) translateX(-100%);
}

.input-range_vertical::before {
  left: 0px;
  top: 524px;
  height: 60px;
  width: 60px;
  text-align: center;
  line-height: 60px;
  z-index: 1;
}

.input-range_vertical::after {
  right: 0px;
  top: 0px;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 69px;
}

.input-range_sun::before {
  content: "";
  background-image: url(../assets/images/icon_sun.svg);
  background-size: cover;
  width: 36px;
  height: 36px;
  top: 14px;
  left: 16px;
}

.input-range_sun::after {
  content: "";
  background-image: url(../assets/images/icon_sun.svg);
  background-size: cover;
  width: 36px;
  height: 36px;
  top: 14px;
  right: 16px;
}

.btn {
  background: #ffffff;
  border-radius: 20px;
  font-size: 13px;
  color: #333333;
  letter-spacing: 0;
  border: none;
  outline: none;
  margin-bottom: 1px;
  padding: 7px 14px;
}

.btn:hover {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
}

.btn:active {
  transform: translateY(1px);
  box-shadow: none;
}

.btn_primary {
  color: #000000;
  background: #ffd93e;
}

.btn_large {
  padding: 24px 30px;
  display: inline-block;
  font-size: 18px;
}

.btn_flex {
  flex-grow: 1;
}

.widget {
  flex-basis: 50%;
  flex-grow: 1;
  border-left: 1rem solid transparent;
  border-right: 1rem solid transparent;
  margin-bottom: 2rem;
}

.widget__header {
  margin-bottom: 2rem;
}

.widget__title {
  font-size: 1.8rem;
  font-weight: bolder;
}

.widget__content {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  min-height: 33rem;
  overflow: hidden;
}

.widget__info-wrapper {
  position: relative;
  width: 100%;
}

.widget__info-wrapper::before {
  content: "";
  display: block;
  padding-top: 52%;
}

.widget__info {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0.25) 100%), url(../assets/images/interior_1280.jpg);
  background-size: cover, cover;
  background-repeat: no-repeat;
  border-radius: 2rem;
}

.widget__column {
  display: inline-block;
}

.widget__column_left {
  float: left;
  width: 65%;
  padding: 2rem 3rem;
}

.widget__column_right {
  float: right;
  width: 35%;
  max-height: 100%;
  height: 100%;
  padding: 1rem 1rem 0 1rem;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
}

.widget__greeting {
  display: block;
  font-size: 2.7rem;
  font-weight: bolder;
  margin-bottom: 1rem;
}

.widget__status {
  display: block;
  font-family: Arial-MT;
  font-size: 1.4rem;
  margin-bottom: 2rem;
}

.widget__weather {
  display: inline-block;
}

.widget__weather:not(:last-child) {
  margin-right: 1rem;
}

.widget__weather-location {
  display: block;
  font-family: ArialMT;
  font-size: 1.4rem;
  color: #858585;
}

.widget__weather-temperature {
  font-family: Arial-BoldMT;
  font-weight: bolder;
  font-size: 3.6rem;
  letter-spacing: 0;
}

.widget__weather-icon {
  width: 2.6rem;
  height: 2.6rem;
}

.action-box {
  position: relative;
  width: 100%;
  max-width: 20rem;
  min-height: 10rem;
  flex-grow: 1;
  margin-bottom: 1rem;
  background: #f7f7f7;
  border-radius: 2rem;
}

.action-box_scroll {
  min-height: 4rem;
  border-radius: 2rem 2rem 0 0;
  flex-grow: 0;
  align-self: flex-end;
  margin-bottom: 0;
  text-align: center;
  line-height: 4rem;
}

.action-box:hover {
  background: #ffffff;
  box-shadow: 0 0.2rem 0.6rem 0 rgba(197, 186, 186, 0.5);
}

.action-box__icon {
  position: absolute;
  left: 1.5rem;
  top: 2rem;
  width: 2.4rem;
  height: 2.4rem;
}

.action-box__icon_scroll {
  width: 1.6rem;
  height: 1.6rem;
  margin-bottom: -0.5rem;
}

.action-box__action {
  position: absolute;
  padding: 0 1.5rem;
  bottom: 1.5rem;
  letter-spacing: 0;
  font-size: 1.4rem;
  font-weight: bolder;
}

.action-box__subscript {
  display: block;
  font-family: ArialMT;
  font-size: 1.2rem;
  color: #858585;
  letter-spacing: 0;
  font-weight: normal;
  margin-top: 0.5rem;
}
