.widget {
  flex-basis: 50%;
  flex-grow: 1;

  border-left: 1rem solid transparent;
  border-right: 1rem solid transparent;

  margin-bottom: 2rem;

  &__header {
    margin-bottom: 2rem;
  }

  &__title {
    font-size: 1.8rem;
    font-weight: bolder;
  }

  &__content {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    min-height: 33rem;
    overflow: hidden;
  }

  &__info-wrapper {
    position: relative;
    width: 100%;

    &::before {
      content: "";
      display: block;
      padding-top: 52%;
    }
  }

  &__info {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    // background-image: url(../assets/images/interior_1280.jpg),
    //   linear-gradient(to right, rgba(#000, 0.8) 0%, rgba(#000, 0.8) 100%);
    background-image: linear-gradient(
        to right,
        rgba(#fff, 0.25) 0%,
        rgba(#fff, 0.25) 100%
      ),
      url(../assets/images/interior_1280.jpg);
    background-size: cover, cover;
    background-repeat: no-repeat;
    border-radius: 2rem;
  }

  &__column {
    display: inline-block;

    &_left {
      float: left;
      width: 65%;
      padding: 2rem 3rem;
    }

    &_right {
      float: right;
      width: 35%;
      max-height: 100%;
      height: 100%;
      padding: 1rem 1rem 0 1rem;
      display: flex;
      flex-flow: column nowrap;
      align-items: center;
      justify-content: flex-start;
      overflow: hidden;
    }
  }

  &__greeting {
    display: block;
    font-size: 2.7rem;
    font-weight: bolder;
    margin-bottom: 1rem;
  }

  &__status {
    display: block;
    font-family: Arial-MT;
    font-size: 1.4rem;
    margin-bottom: 2rem;
  }

  &__weather {
    display: inline-block;
    &:not(:last-child) {
      margin-right: 1rem;
    }

    &-location {
      display: block;
      font-family: ArialMT;
      font-size: 1.4rem;
      color: #858585;
    }

    &-temperature {
      font-family: Arial-BoldMT;
      font-weight: bolder;
      font-size: 3.6rem;
      letter-spacing: 0;
    }

    &-icon {
      width: 2.6rem;
      height: 2.6rem;
    }
  }
}
